<template>
	<div class="sub-container">
		<ul class="tabs sub">
			<li
				@click="goto('/admin/member/all/1')"
				:class="{ current: currentGrade == 'all' }"
				:disabled="currentGrade == 'all'"
			>
				전체
			</li>
			<li
				@click="goto('/admin/member/normal/1')"
				:class="{ current: currentGrade == 'normal' }"
				:disabled="currentGrade == 'normal'"
			>
				일반
			</li>
			<li
				@click="goto('/admin/member/admin/1')"
				:class="{ current: currentGrade == 'admin' }"
				:disabled="currentGrade == 'admin'"
			>
				관리자
			</li>
		</ul>
		<div class="contents-container">
			<ul class="table member">
				<li class="row props">
					<div class="prop check">
						<input
							type="checkbox"
							class="checkbox"
							name="member"
							id="select-all"
							v-model="selectAll"
						/>
						<label for="select-all"></label>
					</div>
					<div class="prop">회원 유형</div>
					<div class="prop">아이디</div>
					<div class="prop">이름</div>
					<div class="prop" style="width: 261px">누적 구매 금액</div>
					<div class="prop">이메일</div>
					<div class="prop">연락처</div>
					<div class="prop">광고수신</div>
				</li>

				<slot v-if="!userList">
					<li class="row no-data body3" style="display: block;">
						등록된 회원이 없습니다
					</li>
				</slot>
				<slot v-else>
					<li class="row" v-for="user in userList" :key="user.id">
						<div class="prop check">
							<input
								type="checkbox"
								class="checkbox"
								name="user_check"
								:id="user.id"
								:value="`${user.id}_${user.name}`"
								v-model="selected"
							/>
							<label :for="user.id"></label>
						</div>
						<div class="prop">{{ user.gradeLabel }}</div>
						<div class="prop clickable" @click="openDetailModalView(user.id)">
							{{ user.identity }}
						</div>
						<div class="prop">{{ user.name }}</div>
						<div class="prop" style="width: 261px">0</div>
						<div class="prop">{{ user.email }}</div>
						<div class="prop">{{ user.phone }}</div>
						<div class="prop">{{ user.agreeYn }}</div>
					</li>
				</slot>
			</ul>

			<ModalView
				v-if="isDetailModalViewed"
				@close-modal="isDetailModalViewed = false"
			>
				<MemberDetail
					@close-modal="isDetailModalViewed = false"
					:user="userDetail"
				></MemberDetail>
			</ModalView>
			<ModalView
				v-if="isUpdateModalViewed"
				@close-modal="isUpdateModalViewed = false"
			>
				<MoveMember
					@close-modal="isUpdateModalViewed = false"
					@confirm="updateGrade"
					:users="selectedUser"
				></MoveMember>
			</ModalView>
			<ModalView
				v-if="isDeleteModalViewed"
				@close-modal="isDeleteModalViewed = false"
			>
				<DeleteMember
					@close-modal="isDeleteModalViewed = false"
					@confirm="deleteUser"
					:users="selectedUser"
				></DeleteMember>
			</ModalView>
			<div class="bottom-container">
				<Pagination
					v-if="totalPage !== -1"
					:listRowCt="15"
					:pageLinkCt="5"
					:totalCount="totalPage"
					:url="`/admin/member/all/${currentGrade}`"
				/>
				<div class="btn-container">
					<button
						type="button"
						class="solid-btn green"
						id="change_btn"
						@click="isUpdateModalViewed = true"
						:disabled="selected.length < 1"
					>
						회원 유형 변경
					</button>
					<button
						class="solid-btn red"
						id="delete_btn"
						@click="isDeleteModalViewed = true"
						:disabled="selected.length < 1"
					>
						회원 삭제
					</button>
				</div>
			</div>
			<LoadingSpinner :loading="isLoading" />
		</div>
	</div>
</template>
<script>
import {
	fetchUserList,
	fetchUserDetail,
	updateGrade,
	deleteUser,
	fetchUserTotalCount,
} from '@/api/user';
import ModalView from '@/components/common/ModalView';
import MemberDetail from '@/views/admin/member/components/MemberDetail';
import MoveMember from '@/views/admin/member/components/MoveMember';
import DeleteMember from './components/DeleteMember.vue';
import Pagination from '@/components/common/Pagination';
import LoadingSpinner from '@/components/common/LoadingSpinner';

export default {
	components: {
		LoadingSpinner,
		ModalView,
		MemberDetail,
		MoveMember,
		DeleteMember,
		Pagination,
	},
	created() {
		this.currentPage = this.$route.params.page;
		this.currentGrade = this.$route.params.grade;
		this.fetchUserList(this.currentPage, this.currentGrade);
		this.fetchUserTotalCount();
	},
	computed: {
		selectedUser() {
			let users = this.selected.map(item => {
				let data = item.split('_');
				return data[1];
			});
			return users;
		},
		selectedId() {
			let ids = this.selected.map(item => {
				let data = item.split('_');
				return parseInt(data[0]);
			});
			return ids;
		},
		selectAll: {
			get: function() {
				return this.userList
					? this.selected.length == this.userList.length
					: false;
			},
			set: function(value) {
				var selected = [];

				if (value) {
					this.userList.forEach(function(item) {
						selected.push(`${item.id}_${item.name}`);
					});
				}
				this.selected = selected;
			},
		},
	},
	data() {
		return {
			isDetailModalViewed: false,
			isUpdateModalViewed: false,
			isDeleteModalViewed: false,
			selected: [],
			userList: [],
			userDetail: {},
			totalPage: -1,
			current: 1,
			currentPage: 0,
			currentGrade: 'all',
			isLoading: false,
		};
	},
	methods: {
		goto(url) {
			this.$router.push({ path: url });
		},
		async fetchUserList(page, grade) {
			this.isLoading = true;
			try {
				let { data } = await fetchUserList(page - 1, grade);
				this.userList = data.map(item => {
					if (item.grade == 'ADMIN') {
						item.gradeLabel = '관리자';
					}
					if (item.grade == 'NORMAL') {
						item.gradeLabel = '일반';
					}
					return item;
				});
			} catch (e) {
				console.log(e);
			} finally {
				this.isLoading = false;
			}
		},
		async openDetailModalView(userId) {
			let { data } = await fetchUserDetail(userId);
			if (data.grade == 'ADMIN') {
				data.gradeLabel = '관리자';
			} else if (data.grade == 'NORMAL') {
				data.gradeLabel = '일반';
			} else {
				data.gradeLabel = '없음';
			}
			this.userDetail = data;
			this.isDetailModalViewed = true;
		},
		async updateGrade(data) {
			this.isLoading = true;
			try {
				let updateInfo = {
					grade: data,
					idList: this.selectedId,
				};
				await updateGrade(updateInfo);
				await this.fetchUserList(this.currentPage, this.currentGrade);
				this.isUpdateModalViewed = false;
			} catch (e) {
				console.log(e);
			} finally {
				this.isLoading = false;
			}
		},
		async deleteUser() {
			this.isLoading = true;
			try {
				await deleteUser(this.selectedId);
				this.isDeleteModalViewed = false;
				this.selected = [];
			} catch (e) {
				console.log(e);
			} finally {
				this.isLoading = false;
			}
		},
		async fetchUserTotalCount() {
			this.isLoading = true;
			try {
				const { data } = await fetchUserTotalCount();
				this.totalPage = data.totalCount;
			} catch (e) {
				alert(e);
			} finally {
				this.isLoading = false;
			}
		},
	},
	watch: {
		$route(to, from) {
			if (to !== from) {
				this.currentPage = this.$route.params.page;
				this.currentGrade = this.$route.params.grade;
				this.fetchUserList(this.currentPage, this.currentGrade);
			}
		},
	},
};
</script>
<style></style>
